import React from 'react';

import { Modal } from '@databricks/design-system';

export function SearchModalBase({
  title,
  visible,
  onCancel,
  children,
}: {
  title: JSX.Element | null;
  visible: boolean;
  onCancel: () => void;
  children?: React.ReactNode;
}) {
  return (
    <Modal title={title} visible={visible} onCancel={onCancel} size="wide" destroyOnClose={true} footer={null}>
      {children}
    </Modal>
  );
}
